.loading__section {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  filter: grayscale(1);
  background-color: #000000f6;
  color: #fff;
  z-index: 1000;
}
